<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">校园圈</p>
            <p class="navBtn f24 cfff pl-30" @click="turnToSend">发布</p>
        </div>
        <div class="item-for">
            <div class="item-list" v-for="(item,index) in list" :key="index" @click="turnToDetail(item.id)">
                <img class="item-img l" :src="$api.fileUrl + item.headImage" alt="">
                <div class="item-content l ml-30">
                    <div class="item-user f26 c333">{{item.teacherName}}</div>
                    <p class="item-titles f18 c666 mt-20">{{item.topic_desc}}</p>
                    <div class="item-bottom mt-30">
                        <span class="item-time l f16 c999">{{item.update_time | fileTime(item.update_time)}}</span>
                        <div class="operating r">
                            <div class="l">
                                <img class="operat-img l mt-8" src="../../../assets/index/operat.png" alt="">
                                <div class="operat-num f16 l ml-10">{{item.replyCount}}</div>
                            </div>
                            <div class="l ml-36">
                                <img class="operat-img l mt-8" src="../../../assets/index/zan.png" alt="">
                                <div class="operat-num f16 l ml-10">{{item.praise_num}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            math:[{name:5,active:true},{name:10,active:false},{name:15,active:false}],
            show:true,
            list:[]
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num).substring(5,publisFn.dateFormat(num).length).replace(/\//g, "-")
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'});//返回上一级
        },
        turnToSend(){
            this.$router.push({path:'/setschoolcircle'});//返回上一级
        },
        turnToDetail(id){
            this.$router.push({path:'/schoolcircledetail',query:{id,id}});//跳转详情
        },
        getSchoolNews(){//获取校园圈列表
            this.$toast.loading({
                mask: false,
                message: '加载中...',
                duration:0
            });
            this.$http({url:this.$api.class.getSchoolNews},{schoolId:JSON.parse(localStorage.getItem('zhxy_user')).schoolId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }

    },
    created() {
        this.getSchoolNews();
    },
}
</script>

<style scoped>
    .centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -3.45rem;}
    .item-for{position: absolute;top: 0.84rem;bottom: 0;overflow: auto;}
    .item-list{padding: 0.3rem;width: 100%;border-bottom:1px solid #f2f2f2;height: 2.57rem;box-sizing: border-box;overflow: hidden;}
    .item-img{width: 1.2rem;height: 1.2rem;border-radius: 50%;display: block;}
    .item-titles{word-wrap:break-word;width: 5.4rem;height: 0.6rem;display: block;}
    .item-bottom{width: 100%;height: 0.5rem;border-radius: 2px;background: #f7f7f7;}
    .item-time{padding: 0 0.3rem;line-height: 0.5rem;overflow: hidden;}
    .operating{overflow: hidden;padding: 0 0.3rem;}
    .operat-img{width: 0.3rem;height: 0.3rem;display: block;}
    .operat-num{color: #929292;line-height: 0.5rem;}
</style>





